html {
  background-color: #f6f6f6; }

body {
  background-color: transparent;
  color: #333;
  font-family: Georgia, "Times New Roman", serif; }

a {
  color: #6C719E;
  transition: color 0.1s, background-color 0.2s;
  text-decoration: none; }

h1,
.alpha {
  font-size: 38px; }

h2,
.beta {
  font-size: 32px; }

h3,
.gamma {
  font-size: 26px; }

h4,
.delta {
  font-size: 21px; }

h5,
.epsilon {
  font-size: 18px; }

h6,
.zeta {
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 0.2em; }

h1 + p,
h1 + form,
h1 + ul,
h1 + ol, h2 + p,
h2 + form,
h2 + ul,
h2 + ol, h3 + p,
h3 + form,
h3 + ul,
h3 + ol, h4 + p,
h4 + form,
h4 + ul,
h4 + ol, h5 + p,
h5 + form,
h5 + ul,
h5 + ol, h6 + p,
h6 + form,
h6 + ul,
h6 + ol {
  margin-top: 0; }

h1 + img, h2 + img, h3 + img, h4 + img, h5 + img, h6 + img {
  margin-top: 1em; }

.left {
  float: left; }

.right {
  float: right; }

.center {
  margin-left: auto;
  margin-right: auto; }

.text-left {
  text-align: left; }

.text-right {
  text-align: right; }

.text-center {
  text-align: center; }

.cover {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0; }

.nav li {
  display: inline; }
  .nav li a {
    display: inline-block; }

.hide-text {
  overflow: hidden;
  text-indent: 100%;
  white-space: nowrap; }

.clearfix {
  *zoom: 1; }
  .clearfix:before,
  .clearfix:after {
    content: '';
    display: table; }
  .clearfix:after {
    clear: both; }

* {
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }

.wrapper {
  max-width: 1180px;
  margin: 0 auto; }

.grids {
  overflow: hidden;
  clear: both;
  margin-left: -3em; }
  .grids > * {
    width: 100%;
    float: left;
    padding-left: 3em; }
    .grids > * *:first-child {
      margin-top: 0; }

.half-gutter {
  margin-left: -1.5em; }
  .half-gutter > * {
    padding-left: 1.5em; }

.alignnone {
  margin: 0.25em 1em 1em 0; }

.aligncenter {
  display: block;
  margin: 0.25em auto 0.25em auto; }

.alignleft {
  float: left;
  margin: 0.25em 1em 1em 0; }

.alignright {
  float: right;
  margin: 0.25em 0 1em 1em; }

.wp-caption {
  max-width: 96%;
  padding: 0.25 0.25em 0.75em;
  background: #fff;
  border: 1px solid #f0f0f0;
  text-align: center; }
  .wp-caption.alignnone {
    margin: 0.25em 1em 1em 0; }
  .wp-caption.alignleft {
    margin: 0.25em 1em 1em 0; }
  .wp-caption.alignright {
    margin: 0.25em 0 1em 1em; }
  .wp-caption img {
    width: auto;
    height: auto;
    margin: 0;
    padding: 0;
    border: 0 none; }
  .wp-caption p.wp-caption-text {
    margin: 0;
    padding: 0 0.25em 0.25em;
    font-size: 0.75em;
    line-height: 1em; }

